export default {
  data() {
    return {
      mountain: {},
    };
  },
  async fetch() {
    this.mountain = await this.$http.$get(
      `https://api.nuxtjs.dev/mountains/${this.$route.params.slug}`
    );
  },
  methods: {
    goBack() {
      return this.$router.go(-1);
    },
  },
};
